import { Row, Col,Rate,Icon  } from 'antd';
import React, {Component} from 'react';
import './InfoCar.css';
import imageCar from 'images/car/hyundai.png';
class ContentInfoCar extends Component{
    render(){
        const rowInfoImgStyle={
            display:'flex',
            flexDirection:'column',
        }
        return(
            <Row >
                <Col md={24} style={rowInfoImgStyle}>
                    <Row className="infor-car" gutter={8}>
                        <Col md={4} className="image_car">
                            <img src={imageCar} width="100%" height="100%" alt="" />
                        </Col>
                        <Col md={15} >
                            <Row className="car_name">
                                <Col md={24}>
                                    <h5>HYUNDAI I10 HATCHBACK 1.0 AT 2017</h5>
                                    <Rate allowClear={false} defaultValue={3} /> 
                                </Col>
                                <Col md={20} className="grid-feature">
                                    <Row style={{display:'flex', flexDirection:'row'}}> 
                                        <Col md={8} className="grid-icon-feature">
                                            <Icon type="select" theme="outlined" />
                                            <span className="text-feature">Xng</span>
                                        </Col>
                                        <Col md={8} className="grid-icon-feature">
                                            <Icon type="select" theme="outlined" />
                                            <span className="text-feature">1.5 Lit</span>
                                        </Col>
                                        <Col md={8} className="grid-icon-feature">
                                            <Icon type="select" theme="outlined" />
                                            <span className="text-feature">S t ng</span>
                                        </Col>
                                    </Row>
                                    <Row style={{display:'flex', flexDirection:'row'}}> 
                                        <Col md={8} className="grid-icon-feature">
                                            <Icon type="select" theme="outlined" />
                                            <span className="text-feature">5 ch</span>
                                        </Col>
                                        <Col md={8} className="grid-icon-feature">
                                            <Icon type="select" theme="outlined" />
                                            <span className="text-feature">Sedan</span>
                                        </Col>
                                        <Col md={8} className="grid-icon-feature">
                                            <Icon type="select" theme="outlined" />
                                            <span className="text-feature">250km/h</span>
                                        </Col>
                                    </Row>
                                </Col>
                            </Row>
                        </Col>
                    </Row>
                    <Row style={{padding:'20px 20px'}}> 
                        <p className="title-label">Tnh nng:</p>
                        <Col md={6}>
                            <Icon type="select" theme="outlined" />
                            <span className="text-feature">iu ha (A/C)</span>
                        </Col>
                        <Col md={6}>
                            <Icon type="select" theme="outlined" />
                            <span className="text-feature">nh v (GPS)</span>
                        </Col>
                        <Col md={6}>
                            <Icon type="select" theme="outlined" />
                            <span className="text-feature">Blutooh</span>
                        </Col>
                        <Col md={6}>
                            <Icon type="select" theme="outlined" />
                            <span className="text-feature">Khe cm USB</span>
                        </Col>
                    </Row>
                    <Row style={{padding:'20px 20px'}}> 
                        <p className="title-label">Th tc: </p>
                        <Col md={6}>
                            <Icon type="select" theme="outlined" />
                            <span className="text-feature">CMND</span>
                        </Col>
                        <Col md={6}>
                            <Icon type="select" theme="outlined" />
                            <span className="text-feature">t cc</span>
                        </Col>
                        <Col md={6}>
                            <Icon type="select" theme="outlined" />
                            <span className="text-feature">Bng li</span>
                        </Col>
                        <Col md={6}>
                            <Icon type="select" theme="outlined" />
                            <span className="text-feature">S h khu</span>
                        </Col>
                    </Row>
                    <Row style={{padding:'20px 20px'}}> 
                        <p className="title-label">Chp nhn thanh ton: </p>
                        <Col md={6}>
                            <Icon type="select" theme="outlined" />
                            <span className="text-feature">Tr sau</span>
                        </Col>
                    </Row>
                    <Row style={{padding:'20px 20px'}}> 
                        <p className="title-label">Ghi ch: </p>
                        <Col md={24}>
                            <p className="text-note">- CMND: Bn gc </p>
                            <p className="text-note">- t cc: gi tr t 20 triu ng tr ln hoc ti sn tng ng</p>
                            <p className="text-note">- S h khu: Bn gc (h khu TP hoc Giy php ng k kinh doanh)</p>
                        </Col>
                    </Row>
                </Col>
            </Row>
        )
    }
}
export default ContentInfoCar;