import { Menu, Icon } from 'antd';
import React , {Component} from 'react';
import {Link} from 'react-router-dom';
import './index.css';
const SubMenu = Menu.SubMenu;
const MenuItemGroup = Menu.ItemGroup;

class Sider extends Component {
  handleClick = (e) => {
    console.log('click ', e);
  }

  render() {
    return (
      <Menu
        onClick={this.handleClick}
        style={{ width: 256 }}
        defaultSelectedKeys={['1']}
        defaultOpenKeys={['sub1']}
        mode="inline"
      >
        <SubMenu key="sub1" title={<span><Icon type="mail" /><span>Thng tin</span></span>}>
            <Menu.Item key="5">Cp nht mt khu</Menu.Item>
            <Menu.Item key="6">Cp nht thng tin</Menu.Item>
            <SubMenu key="sub3" title="Gii thiu">
                <Menu.Item key="7">H thng</Menu.Item>
                <Menu.Item key="8">C nhn</Menu.Item>
            </SubMenu>
        </SubMenu>
        <SubMenu key="sub2" title={<span><Icon type="appstore" /><span>Qun l </span></span>}>
          
          <MenuItemGroup key="g1" title="Xe">
            <Menu.Item key="1">
              <Link to="management/car">
                Danh sch xe cho thu
              </Link>
            </Menu.Item>
          </MenuItemGroup>
          <MenuItemGroup key="g2" title="n hng">
            <Menu.Item key="3">Danh sch n hng</Menu.Item>
          </MenuItemGroup>
        </SubMenu>
      </Menu>
    );
  }
}
export default Sider;